import { Avatar, Card, CardHeader, Skeleton, Typography, CircularProgress } from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorIcon from '@mui/icons-material/Error';
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function CyberspacemediaAPI({ onStatusChange }) {
  const [loading, setloading] = useState(false);
  const [status, setstatus] = useState('Loading');
  const [color, setcolor] = useState('');
  const apiURL = 'https://findresults.online/trafficshield/API/cyberspace_api';

  useEffect(() => {
    async function fetchApiStatus() {
      try {
        const response = await axios.get(apiURL);
        const data = response.data;
        if (data.http_code === 200) {
          setloading(true);
          setcolor('green');
          onStatusChange(false);
          setstatus(data.http_code);
        } else {
          setloading(true);
          setcolor('red');
          onStatusChange(true);
          setstatus(data.http_code);
        }
      } catch (error) {
        setloading(false);
      }
    }

    fetchApiStatus();
  }, [onStatusChange]);

  return (
    <div style={{ marginTop: '10px' }}>
      <Card variant="elevation" elevation={5}  sx={{display: 'flex', alignItems: 'center', width: '70%', height: '50px', margin: 'auto' }}>
        <CardHeader
          avatar={
            <Avatar>
              {loading ? (
                <img src="https://trafficshield.io/wp-content/uploads/2022/11/cropped-black-logo-254x58-1.webp" alt="Avatar" style={{ width: '100%', height: '100%', objectFit: 'cover', backgroundColor: 'transparent' }} />
              ) : (
                <Skeleton variant="circular" width={40} height={40} />
              )}
            </Avatar>
          }
        />
        <div style={{ display: 'flex', flexDirection: 'column', flexGrow: 1 }}>
          {loading ? (
            <Typography>HNCyberSpace </Typography>
          ) : (
            <Skeleton variant="rectangular" animation="pulse" sx={{ width: '80%', height: '30px' }}></Skeleton>
          )}
        </div>

        <div style={{ marginLeft: 'auto', marginRight: '10px', display: 'flex', alignItems: 'center' }}>
          {loading ? (
            <>
              {status}
              {status !== 200 ? (
                <ErrorIcon sx={{ color:'red', fontSize: '30px' }} />
              ) : (
                <CheckCircleIcon sx={{ color: { color }, fontSize: '30px', }} />
              )}
            </>
          ) : (
            <CircularProgress size={20} />
          )}
        </div>
      </Card>
    </div>
  );
}

export default CyberspacemediaAPI;
