import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import { Alert } from '@mui/material';
import Nav from './Nav';
import ProxyCheck from './API/ProxyCheck';
import TrafficshieldAPI from './API/TrafficshieldAPI';
import CyberspacemediaAPI from './API/CyberspacemediaAPI';

export default function Home() {
  const [componentMounted, setComponentMounted] = useState(true);
  const [modalTrigger, setModalTrigger] = useState(false);

  const handleStatusChange = (status) => {
    console.log('Status changed:', status);
    setModalTrigger(status);
  };

  const handleRefresh = () => {
    setComponentMounted(false);
    setTimeout(() => {
      setComponentMounted(true);
    }, 100);
  };

  useEffect(() => {
    const refreshComponents = () => {
      setComponentMounted(false);
      setTimeout(() => {
        setComponentMounted(true);
      }, 5000);
    };

    const intervalId = setInterval(refreshComponents, 300000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <div style={{ paddingTop: '50px' }}>
      {modalTrigger ? (
        <Alert variant="filled" severity="error">
          !Error: Please check one or more of your API is down
        </Alert>
      ) : (
        <></>
      )}

      <Nav onRefresh={handleRefresh} />
      {componentMounted ? (
        <>
          <ProxyCheck onStatusChange={handleStatusChange} />
          <TrafficshieldAPI onStatusChange={handleStatusChange} />
          <CyberspacemediaAPI onStatusChange={handleStatusChange} />
        </>
      ) : (
        <Box sx={{ width: '100%' }}>
          <LinearProgress />
        </Box>
      )}
    </div>
  );
}
