import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Avatar, Card, CardHeader, CircularProgress, Skeleton, Typography } from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

function ProxyCheck() {

    const [loading, setLoading] = useState(false);
    const [color, setcolor] = useState('red');
    // eslint-disable-next-line no-unused-vars
    const [status, setstatus] = useState('Loading');
    const apiUrl = "https://findresults.online/trafficshield/API/get_ip_details";

    useEffect(() => {
        async function fecthStatus() {
            try {
                const response = await axios.get(apiUrl);
                const data = response.data;
                setLoading(true);

                if (data.status === 'ok') {
                    setstatus('OK');
                    setcolor('green');
                } else if (data.status === 'warning') {
                    setstatus('Warning');
                    setcolor('yello');
                }
            } catch (error) {
                setstatus('Error');
                setcolor('red');
                setLoading(false);
            }
        }
        fecthStatus();
    }, []);

    return (
        <div style={{ marginTop: '10px' }}>
            <Card variant='elevation' elevation={5} sx={{ display: 'flex', alignItems: 'center', width: '70%', height: '50px', margin: 'auto' }}>
                <CardHeader
                    avatar={
                        <Avatar>
                            {loading ?
                                (
                                    <img src="https://avatars.githubusercontent.com/u/27004845?v=4" alt="Avatar" style={{ width: '100%', height: '100%', objectFit: 'cover', backgroundColor: 'transparent' }} />
                                )
                                : (
                                    <Skeleton variant="circular" width={40} height={40} />
                                )}
                        </Avatar>
                    }
                />

                <div style={{ display: 'flex', flexDirection: 'column', flexGrow: 1 }}>
                    {loading ?
                        (
                            <Typography>ProxyCheck API</Typography>
                        )
                        : (
                            <Skeleton variant='rectangular' animation="pulse" sx={{ width: '80%', height: '30px' }}></Skeleton>
                        )}
                </div>

                
                <div style={{ marginLeft: 'auto', marginRight: '10px', display: 'flex', alignItems: 'center' }}>
                    {loading ?
                        (
                            <> {status}
                                <CheckCircleIcon sx={{ color: {color}, fontSize: '30px' }} />
                            </>
                        )
                        : (
                            <CircularProgress size={20} />
                        )}


                </div>


               
            </Card>

        </div>

    );
}

export default ProxyCheck;
